import React from "react";
import Zoom from "react-reveal/Zoom";
import Fade from "react-reveal/Fade";
import styled from "styled-components";
import shophub from "../assets/shophub.png";
import morvii from "../assets/morvii.png";
const Container = styled.div`
  background: #111;
`;

export default class Projects extends React.PureComponent {
  componentDidMount() {}
  onMouseOut(e) {
    e.preventDefault();
    e.target.style.transform = "translate(-50%,0)";
  }
  onMouseMove(e) {
    e.preventDefault();
    e.target.style.transform = `translate(calc(-50% + ${e.movementX * 2}px),${
      e.movementY * 2
    }px) scale(1.05)`;
  }
  render() {
    return (
      <Container>
        <div className="header__content">
          <Zoom>
            <div>
              <div className="header__content_line"></div>
            </div>
          </Zoom>
          <Fade bottom>
            <div className="h2 h2-light">Projects</div>
          </Fade>
          <Card
            title="Morvii"
            img={morvii}
            body="A fully functional android application like instagram"
            languages="React, Django, Node.js"
            buttons={
              <span>
                <a
                  target="_blank"
                  rel="noreferrer"
                  href="https://morwii.github.io"
                  class="btn btn-primary">
                  View Features
                </a>
                <a
                  target="_blank"
                  rel="noreferrer"
                  style={{ marginLeft: 10 }}
                  href="https://github.com/niteshdangi/morvii"
                  class="btn btn-primary">
                  GitHub
                </a>
              </span>
            }
          />
          <Card
            title="ShopHub"
            img={shophub}
            body="A web application for ecommerce with offline access. Currently only frontend is working with few pages"
            languages="React"
            buttons={
              <span>
                <a
                  target="_blank"
                  rel="noreferrer"
                  href="https://niteshdangi.github.io/shophub"
                  class="btn btn-primary">
                  Visit Website
                </a>
                <a
                  target="_blank"
                  rel="noreferrer"
                  style={{ marginLeft: 10 }}
                  href="https://github.com/niteshdangi/shophub"
                  class="btn btn-primary">
                  GitHub
                </a>
              </span>
            }
          />
        </div>
      </Container>
    );
  }
}
const Card = ({ img, title, body, languages, buttons }) => (
  <Fade bottom>
    <div style={{ marginTop: 50 }}>
      <div
        className="card mb-3"
        style={{
          left: "50%",
          transform: "translateX(-50%)",
          textAlign: "left",
        }}>
        <div className="row no-gutters">
          <div
            style={{
              justifyContent: "center",
              alignItems: "center",
              display: "flex",
            }}
            className="col-md-4">
            <img src={img} className="card-img" alt={title} />
          </div>
          <div className="col-md-8">
            <div className="card-body">
              <h5 className="card-title">{title}</h5>
              <p className="card-text">{body}</p>
              <p className="card-text">
                <small className="text-muted">{languages}</small>
              </p>
              <p>{buttons}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Fade>
);
