import React from "react";
import Zoom from "react-reveal/Zoom";
import Fade from "react-reveal/Fade";
import styled from "styled-components";
const Container = styled.div`
  background: #111;
`;
const Skill = styled.div`
  border: 1px solid #555;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: grid;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
`;
const Col = ({ url, onMouseMove, onMouseOut, title, width = 50 }) => (
  <div className="col">
    <Fade bottom>
      <div>
        <Skill
          title={title}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          className="skill">
          <img src={url} alt="" width={width} />
        </Skill>
      </div>
    </Fade>
  </div>
);
export default class About extends React.PureComponent {
  componentDidMount() {}
  onMouseOut(e) {
    e.preventDefault();
    e.target.style.transform = "translate(-50%,0)";
  }
  onMouseMove(e) {
    e.preventDefault();
    e.target.style.transform = `translate(calc(-50% + ${e.movementX * 2}px),${
      e.movementY * 2
    }px) scale(1.05)`;
  }
  render() {
    return (
      <Container>
        <div className="header__content">
          <Zoom>
            <div>
              <div className="header__content_line"></div>
            </div>
          </Zoom>
          <Fade bottom>
            <div className="h2 h2-light">GET TO KNOW ME!</div>
          </Fade>
          <Fade bottom>
            <div style={{ marginTop: 50 }}>
              <div
                className="about_para small-caps-2 mt-1"
                style={{
                  maxWidth: 700,
                  left: "50%",
                  position: "relative",
                  transform: "translateX(-50%)",
                }}>
                I am a 3rd year student pursuing Bachelor's of Technology in
                Computer Science. I am an avid learner and always curious to
                learn about new technologies.
              </div>
            </div>
          </Fade>
          <div className="container" style={{ marginTop: 70 }}>
            <div className="row">
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Html5"
                url="https://www.freepnglogos.com/uploads/html5-logo-png/html5-logo-html-logo-2.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="CSS3"
                url="https://www.freepnglogos.com/uploads/html5-logo-png/html5-logo-css-logo-png-transparent-svg-vector-bie-supply-9.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                width={70}
                title="Javascript"
                url="https://www.freepnglogos.com/uploads/javascript-png/javascript-logo-transparent-logo-javascript-images-3.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="PHP"
                url="https://www.php.net/images/logos/new-php-logo.png"
              />
            </div>
            <div className="row">
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                width={90}
                title="Django"
                url="https://cdn.freebiesupply.com/logos/thumbs/2x/django-community-logo.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Python"
                url="https://assets.stickpng.com/images/5848152fcef1014c0b5e4967.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                width={70}
                title="React"
                url="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1280px-React-icon.svg.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Java"
                url="https://assets.stickpng.com/images/58480979cef1014c0b5e4901.png"
              />
            </div>

            <div className="row">
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Android Studio"
                url="https://2.bp.blogspot.com/-tzm1twY_ENM/XlCRuI0ZkRI/AAAAAAAAOso/BmNOUANXWxwc5vwslNw3WpjrDlgs9PuwQCLcBGAsYHQ/s1600/pasted%2Bimage%2B0.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Excel"
                url="https://upload.wikimedia.org/wikipedia/commons/thumb/8/86/Microsoft_Excel_2013_logo.svg/1043px-Microsoft_Excel_2013_logo.svg.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="CodeIgnitor"
                url="https://assets.stickpng.com/images/5847eb8bcef1014c0b5e4851.png"
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Expo"
                url="https://www.jonathanhelvey.com/static/expo-a60a7cc250f47bbd0ee473b576c6c476.png"
              />
            </div>
          </div>
        </div>
      </Container>
    );
  }
}
