import React from "react";
import styled from "styled-components";
import Loader from "../utils/Loader";
import { Parallax } from "react-scroll-parallax";
import HomeImg from "../assets/home.png";
import About from "./About";
import Projects from "./Projects";
const Image = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${HomeImg});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
`;
const ImageOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    0,
    rgba(17, 17, 17, 1),
    rgba(17, 17, 17, 0.7),
    rgba(17, 17, 17, 0.5)
  );
`;
const DataOverlay = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  margin: auto;
  transform: translate(-50%, -50%);
`;
const Button = styled.a`
  position: relative;
  display: inline-block;
  letter-spacing: 1px;
  line-height: 1.3;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  cursor: pointer;
  outline: none;
  box-shadow: none;
  border-width: 2px;
  border-style: solid;
  border-radius: 64px;
`;
export default class Home extends React.PureComponent {
  componentDidMount() {
    setTimeout(() => {
      document.querySelector(".preloader")?.classList?.add("hide__preloader");
      document.querySelector(".content")?.classList?.add("loaded");
      setTimeout(() => {
        document.querySelector(".preloader")?.remove();
      }, 3010);
    }, 500);
  }
  render() {
    return (
      <div>
        <Loader />
        <div style={{ height: "100vh" }}>
          <Image className="content">
            <ImageOverlay />
            <Parallax
              y={[-20, 20]}
              styleInner={{ height: "100%" }}
              styleOuter={{ height: "100%" }}>
              <DataOverlay>
                <h1 className="xl xl-light" style={{ margin: 0 }}>
                  <div className="split-text__line">
                    <div className="split-text__char">N</div>
                    <div className="split-text__char">i</div>
                    <div className="split-text__char">t</div>
                    <div className="split-text__char">e</div>
                    <div className="split-text__char">s</div>
                    <div className="split-text__char">h</div>
                  </div>
                </h1>
                <div className="h4 h4-light">
                  <div className="split-text__line">
                    <div className="split-text__char">Creative &nbsp;</div>
                    <div className="split-text__char">Thinking &nbsp;</div>
                    <div className="split-text__char">Never &nbsp;</div>
                    <div className="split-text__char">Ends</div>
                    <div className="split-text__char">!</div>
                  </div>
                </div>
                <div style={{ marginTop: 50 }}>
                  <Button
                    data-hover="Download Resume"
                    className="button"
                    target="_blank"
                    rel="noreferrer"
                    href="https://niteshdangi.github.io/resume.pdf">
                    <span className="button__label-hover">Download Resume</span>
                  </Button>
                </div>
              </DataOverlay>
            </Parallax>
          </Image>
        </div>
        <About />
        <Projects />
        <div style={{ height: "100vh", overflow: "hidden" }}>
          <div style={{ height: "100%" }}>
            <ImageOverlay
              style={{
                top: "unset",
                background:
                  "linear-gradient( 180deg, rgba(17,17,17,1), rgba(17,17,17,0.7), rgba(17,17,17,0.5) )",
              }}></ImageOverlay>
            <div
              style={{
                background:
                  "url(https://source.unsplash.com/1600x900/?coding,laptop-code,contactme)",
                backgroundPosition: "center",
                backgroundSize: "cover",
                position: "absolute",
                width: "100%",
                height: "100%",
                zIndex: -1,
              }}></div>
            <Parallax
              y={[-40, 40]}
              styleInner={{ height: "100%" }}
              styleOuter={{ height: "100%" }}>
              <DataOverlay style={{ position: "relative" }}>
                <div className="h4 h4-light" style={{ fontFamily: "Raleway" }}>
                  Hit me up if you'd like to collaborate or discuss books with
                  me!
                </div>
                <div
                  className="row"
                  style={{
                    maxWidth: 150,
                    position: "relative",
                    left: "50%",
                    transform: "translateX(-50%)",
                  }}>
                  <div className="col">
                    <a
                      className="social"
                      href="https://github.com/niteshdangi"
                      target="_blank"
                      rel="noreferrer">
                      <img
                        src="https://cdn2.iconfinder.com/data/icons/social-icons-grey/512/GITHUB-512.png"
                        width={40}
                        alt=""
                      />
                    </a>
                  </div>
                  <div className="col">
                    <a
                      className="social"
                      href="https://linkedin.com/in/niteshdangi"
                      target="_blank"
                      rel="noreferrer">
                      <img
                        src="https://icon-library.com/images/linkedin-icon-no-background/linkedin-icon-no-background-29.jpg"
                        width={40}
                        alt=""
                      />
                    </a>
                  </div>
                </div>
              </DataOverlay>
            </Parallax>
          </div>
        </div>
      </div>
    );
  }
}
